import { useCallback, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Modal, ModalHeader, ModalSize } from '@/components/ExtendedModal';
import { useWindowDimensions } from '@/hooks';
import { TSpan } from '@/components/Typography';
import { Infinite } from '@/components/Infinite';
import { useListBlockedCountriesHelper } from '@/features/noums/hooks/spaceQuery';
import { type BlockedCountry } from '@/apollo/generated/types';
import { Icon } from '@/components/Icon';
import { Button } from '@/components/Button';
import * as S from '../../styles';

export type TModalProps = {
  isOpen: boolean;
  onClose: () => void;
};

export const BlockedCountriesListModal = ({ isOpen, onClose }: TModalProps) => {
  const [countries, setCountries] = useState<BlockedCountry[]>([]);
  const { t } = useTranslation();
  const windowSize = useWindowDimensions();
  const isMobile = useMemo(() => windowSize.width < 768, [windowSize]);
  const pageSize = isMobile ? 30 : 20;
  const { data, fetchMore } = useListBlockedCountriesHelper({
    limit: pageSize,
  });

  useEffect(
    () => setCountries(data?.listBlockedCountries?.data as BlockedCountry[]),
    [data?.listBlockedCountries?.data],
  );

  const onFetchMore = useCallback(async () => {
    const { data: moreCountries } = await fetchMore({
      variables: {
        limit: pageSize,
        offset: countries.length || 0,
      },
    });
    setCountries([
      ...countries,
      ...(moreCountries.listBlockedCountries?.data as BlockedCountry[]),
    ]);
  }, [countries, fetchMore, pageSize]);

  return (
    <Modal
      testId="chamber-blocked-countries-modal"
      open={isOpen}
      onClose={onClose}
      enableAnimation
      size={ModalSize.M}
      customCloseButton={
        <Button
          testId="close_reference_modal"
          textOnly
          size="small"
          icon={
            <Icon
              color="icon-button-brand-primary-default"
              name="arrow_left_m"
              size={24}
            />
          }
          onClick={onClose}
        />
      }
    >
      <ModalHeader>
        {t(`noumena.chamber_edit.visibility.global_availability`)}
      </ModalHeader>
      <S.CountryDescription>
        <TSpan colorToken="--text-modal-neutral-default" font="body-m">
          {t(`noumena.chamber_edit.visibility.except_countries`)}
        </TSpan>
      </S.CountryDescription>
      {countries?.length ? (
        <Infinite
          onFetchMore={onFetchMore}
          status="end-with-force"
          paddingBottom="15px"
          paddingTop="15px"
          width="100%"
        >
          {countries?.map((country) => (
            <S.Container key={country.code}>
              <S.CountryName>
                <TSpan colorToken="--text-modal-neutral-default" font="body-m">
                   {country?.name || ''}
                </TSpan>
              </S.CountryName>
            </S.Container>
          ))}
        </Infinite>
      ) : (
        <TSpan colorToken="--text-modal-neutral-default">
          no blocked countries
        </TSpan>
      )}
    </Modal>
  );
};

export default BlockedCountriesListModal;
