import { useEffect } from 'react';
import {
  type ListBlockedCountriesQueryVariables,
  useListBlockedCountriesLazyQuery,
} from '@/apollo/graphql';
import { useAuth } from '@/features/auth/contexts';

export const useListBlockedCountriesHelper = (
  variables?: ListBlockedCountriesQueryVariables,
) => {
  const { user } = useAuth();

  const [getListBlockedCountries, { data, loading, refetch, fetchMore }] =
    useListBlockedCountriesLazyQuery({
      variables,
    });

  useEffect(() => {
    if (user?._id) {
      getListBlockedCountries();
    }
  }, [user, getListBlockedCountries]);

  return {
    data,
    loading,
    refetch,
    fetchMore,
    getListBlockedCountries,
  };
};
